
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.w200 {
  width: 200px;
}
.disable-mask {
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.1);
}
.btn.px-btn {
  padding: 4px 15px;
  border-radius: 2px;
}
input:disabled {
  background-color: #fff;
}

.wrap {
  overflow: hidden;
}
.color-folder {
  color: #FE9E0F;
}
.color-file {
  color: #ccc;
}
.color-green {
  color: #00B996;
}
.hover-bg:hover {
  background-color: #f2f2f2;
}
.left-menu li {
  cursor: pointer;
}
.left-menu .new-folder-input {
  width: 110px;
  padding: 5px 10px;
  border: 1px #cdcdcd solid;
  border-radius: 2px;
}
.left-menu .new-folder-input:focus {
    outline: 1px #22a7f0 solid;
    border-radius: 2px;
}
.left-menu .menu-top > li:not(:first-child) {
  border-bottom: 1px #cdcdcd dashed;
}
.left-menu .menu-top ul {
  font-size: 14px;
  line-height: 30px;
  color: #666;
  margin-top: -1px;
  background-color: #fff;
  border-bottom: 1px #cdcdcd dashed;
}
.left-menu .menu-top ul li:first-child:before {
    height: 15px;
    top: 0;
}
.left-menu .menu-top ul li {
    position: relative;
    padding-left: 35px;
    padding-right: 10px;
}
.left-menu .menu-top ul li:before {
      content: '';
      position: absolute;
      width: 13px;
      height: 30px;
      border-bottom: 1px #999 dashed;
      border-left: 1px #999 dashed;
      left: 20px;
      top: -15px;
}
.right-content {
  left: 200px;
}
.right-content .title input {
    border: none;
    width: 45%;
}
.right-content .title input:focus {
      outline: none;
}

.login-wrap[data-v-4b1eeeea] {
  margin: 200px auto;
}
input[data-v-4b1eeeea] {
  width: 150px;
  border: 1px #333 solid;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  border-radius: 2px;
  padding: 0 15px;
  margin-bottom: 15px;
}
